.linear-gradient-horizontal {
  background: linear-gradient(90deg, #FF9800 0%, #FF5F00 100%);
}
.linear-gradient-horizontal-opacity {
  background: linear-gradient(90deg, rgba(255, 152, 0, 0.5) 0%, rgba(255, 95, 0, 0.5) 100%);
}
.linear-gradient-vertical {
  background: linear-gradient(135deg, #FF9800 0%, #FF5F00 100%);
}
.primary-box-shadow {
  box-shadow: 0 5px 10px 0 rgba(255, 141, 0, 0.3);
}
#app h1 {
  font-size: 18px;
  font-weight: 600;
}
#app h2 {
  font-size: 16px;
}
#app .weui-btn {
  border-radius: 100px;
  line-height: 2.6;
}
#app .weui-btn_primary {
  background: linear-gradient(90deg, #FF9800 0%, #FF5F00 100%);
  box-shadow: 0 5px 10px 0 rgba(255, 141, 0, 0.3);
}
#app .weui-btn_primary.weui-btn_disabled {
  position: relative;
}
#app .weui-btn_primary.weui-btn_disabled::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.3);
}
#app .weui-btn_plain-primary {
  border: 2px solid #FF5F00;
  color: #FF6600;
  box-shadow: 0 5px 10px 0 rgba(255, 141, 0, 0.3);
}
#app .weui-cell {
  padding: 12px 16px;
}
#app .weui-cells {
  margin-top: 0;
}
#app .weui-cells::before {
  content: none;
}



